import React from 'react';
import cn from 'classnames';
import { Product } from '../Product/Product';

export const ProductTable = (
  { products, sortType, isReversed, onSortTypeChange, onIsReversedChange },
) => {
  function sortBy(newSortType) {
    const firstClick = newSortType !== sortType;
    const secondClick = newSortType === sortType && !isReversed;
    const thirdClick = newSortType === sortType && isReversed;

    if (firstClick) {
      onSortTypeChange(newSortType);
      onIsReversedChange(false);

      return;
    }

    if (secondClick) {
      onSortTypeChange(newSortType);
      onIsReversedChange(true);

      return;
    }

    if (thirdClick) {
      onSortTypeChange('');
      onIsReversedChange(false);
    }
  }

  return (
    <table
      data-cy="ProductTable"
      className="table is-striped is-narrow is-fullwidth"
    >
      <thead>
        <tr>
          <th>
            <span className="is-flex is-flex-wrap-nowrap">
              ID

              <a
                href="#/"
                onClick={() => sortBy('ID')}
              >
                <span className="icon">
                  <i
                    data-cy="SortIcon"
                    className={cn('fas', {
                      'fa-sort': sortType !== 'ID',
                      'fa-sort-down': sortType === 'ID' && isReversed,
                      'fa-sort-up': sortType === 'ID' && !isReversed,
                    })}
                  />
                </span>
              </a>
            </span>
          </th>

          <th>
            <span className="is-flex is-flex-wrap-nowrap">
              Product

              <a
                href="#/"
                onClick={() => sortBy('Product')}
              >
                <span className="icon">
                  <i
                    data-cy="SortIcon"
                    className={cn('fas', {
                      'fa-sort': sortType !== 'Product',
                      'fa-sort-down': sortType === 'Product'
                        && isReversed,
                      'fa-sort-up': sortType === 'Product'
                        && !isReversed,
                    })}
                  />
                </span>
              </a>
            </span>
          </th>

          <th>
            <span className="is-flex is-flex-wrap-nowrap">
              Category

              <a
                href="#/"
                onClick={() => sortBy('Category')}
              >
                <span className="icon">
                  <i
                    data-cy="SortIcon"
                    className={cn('fas', {
                      'fa-sort': sortType !== 'Category',
                      'fa-sort-down': sortType === 'Category'
                        && isReversed,
                      'fa-sort-up': sortType === 'Category'
                        && !isReversed,
                    })}
                  />
                </span>
              </a>
            </span>
          </th>

          <th>
            <span className="is-flex is-flex-wrap-nowrap">
              User

              <a
                href="#/"
                onClick={() => sortBy('User')}
              >
                <span className="icon">
                  <i
                    data-cy="SortIcon"
                    className={cn('fas', {
                      'fa-sort': sortType !== 'User',
                      'fa-sort-down': sortType === 'User'
                        && isReversed,
                      'fa-sort-up': sortType === 'User'
                        && !isReversed,
                    })}
                  />
                </span>
              </a>
            </span>
          </th>
        </tr>
      </thead>

      <tbody>
        {products.map(product => (
          <Product
            product={product}
            key={product.key}
          />
        ))}
      </tbody>
    </table>
  );
};
